





















































@import "~styles/base";
.threeQUpload .threeQUpload-preview {
  border: 1px solid @color-light-grey;
  display: flex;

  .threeQUpload-preview-image {
    width: 400px;

    img {
      display: block;
      width: 100%;
    }
  }

  .threeQUpload-preview-meta {
    flex: 1 1 auto;
    padding: @space-3;

    > div {
      &:not(:last-child) {
        margin-bottom: @space-2;
      }
    }

    strong {
      display: block;
    }
  }
}
